import {combineReducers} from 'redux'
import { FILTER, DATA_RECEIVED } from '../actions/types'

const data = (state = [], action) => {
    switch(action.type){
        case DATA_RECEIVED: 
            return action.payload
        default:
            return state
    }
}

const filters = (state = {
    search: '',
    type: 0,
    has: 0,
    status: 0,
    notes: 0
}, action) => {
    switch(action.type){
        case FILTER:
            return {
                ...state,
                [action.payload.by]: action.payload.value
            }
        default:
            return state
    }
}

export default combineReducers({
    filters,
    data
})