import React from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import './index.css';
import configureStore from "./store";
import { Provider } from "react-redux";
import App from './components/App';
import * as serviceWorker from './serviceWorker';
import { dataReceived } from './actions';

const store = configureStore()

const filteredList = [
    { id: 0, created: 'TD', type: 1, first: 'Joe', last: 'Best', test: 'Math', centre: 'London branch', date: '12/01/2020', time: 1, has: 1, status: 2, note: "" },
    { id: 1, created: 'TD', type: 2, first: 'Kevin', last: 'Hart', test: 'English A Levels', centre: 'London branch', date: '12/01/2020', time: 2, has: 2, status: 1, note: "fdsfsd" },
    { id: 2, created: 'TD', type: 2, first: 'Chester', last: 'Bernie', test: 'English A Levels', centre: 'London branch', date: '12/01/2020', time: 2, has: 3, status: 3, note: "" }
]

store.dispatch(dataReceived(filteredList))

ReactDOM.render(
<Provider store={store}>
    <App />
</Provider>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
