import React, {useState} from 'react'
import {Container, Row, Col, InputGroup, FormControl, Button, ButtonToolbar, ToggleButton, ToggleButtonGroup, Accordion} from 'react-bootstrap'
import { KeyboardArrowDown } from 'styled-icons/material'
import { Filter } from 'styled-icons/boxicons-regular/Filter'
import { Globe } from "styled-icons/boxicons-regular/Globe"
import { PhoneInTalk } from "styled-icons/material/PhoneInTalk"
import { Dashboard } from "styled-icons/boxicons-solid/Dashboard";
import styled from 'styled-components'
import { connect } from "react-redux"
import {filterBy} from '../actions'
import {filtersSelector, filteredListSelector} from '../selectors'

import DataSort from "react-data-sort";

const TopSection = styled(Col)`
background: rgb(33,172,140);
background: linear-gradient(90deg, rgba(33,172,140,1) 0%, rgba(30,138,178,1) 100%);
padding-top:15px;
color:#fff;
`

const MainSection = styled(Col)`
min-height:500px
`

const App = ({ filterBy, filters, filteredList = [] /* redux */, defaultSortBy = 'created', searchInKeys = ['name'], itemsPerPage = 25 }) => {
    const [searchInput, setSearchInput] = useState(filters.search)

    /* Search */
    const handleSearchInputChange = e => {
        setSearchInput(e.target.value)
        filterBy({ by: 'search', value: e.target.value })
    }

    /* Filters */
    const handleTypeChange = value => filterBy({ by: 'type', value })
    const handleHasChange = value => filterBy({ by: 'has', value })
    const handleStatusChange = value => filterBy({ by: 'status', value })
    const handleNotesChange = value => filterBy({ by: 'notes', value })

    return (
        <Container fluid={true}>
            <Row className="mb-3">
                <Accordion as={TopSection} defaultActiveKey="0">
                    <Container>
                        <Row>
                            <Col sm={12} className="d-flex mb-3 align-items-end">
                                <Dashboard size={21} />
                                <h6 className="m-0 ml-1">Manage client bookings</h6>
                                <div className="bg-white text-dark rounded d-inline-block px-2 py-1 d-flex align-items-center ml-auto" style={{width:180}}>
                                    <div className="rounded-circle bg-secondary p-2 mr-3 text-light d-flex justify-content-center align-items-center" style={{width:45, height: 45}}>10</div>
                                    <div>
                                        <small className="text-muted">Today</small><br />
                                        <b>Â£212</b>
                                    </div>
                                    <KeyboardArrowDown size={24} className="ml-auto"/>
                                </div>
                            </Col>
                            <Col sm={12} className="mb-3">
                                <InputGroup className="bg-white rounded p-2">
                                    <FormControl className="border-0" placeholder="Search by name, test name or test centre" value={searchInput} onChange={handleSearchInputChange} />
                                    <InputGroup.Append>
                                        <Button variant="link" className="border-0">
                                            <Accordion.Toggle eventKey="0" as={Filter} size={28} className="text-dark" title="Open filter options"/>
                                        </Button>
                                    </InputGroup.Append>
                                </InputGroup>
                            </Col>
                            <Col sm={12} className="mb-3">
                                <Accordion.Collapse eventKey="0">
                                    <div className="d-flex">
                                        <style type="text/css">
                                        {`
                                        .btn-filter {
                                            color:#999;
                                            padding:3px 10px;
                                            font-size:14px;
                                            margin:0 10px
                                        }
                                        .btn-filter.active {
                                            background:#0098E8;
                                            color:#fff;
                                            border-radius:3px!important
                                        }
                                        `}
                                        </style>
                                        {/* Type */}
                                        <ButtonToolbar className="bg-white d-inline-block rounded py-2 text-dark">
                                            <ToggleButtonGroup type="radio" name="type" defaultValue={filters.type} onChange={handleTypeChange}>
                                                <ToggleButton variant="filter" value={0}>All</ToggleButton>
                                                <ToggleButton variant="filter" value={1}>
                                                    <Globe size={18}/>
                                                </ToggleButton>
                                                <ToggleButton variant="filter" value={2}>
                                                    <PhoneInTalk size={18} />
                                                </ToggleButton>
                                            </ToggleButtonGroup>
                                        </ButtonToolbar>
                                        {/*  */}
                                        <ButtonToolbar className="bg-white d-inline-block rounded py-2 text-dark ml-auto">
                                            <ToggleButtonGroup type="radio" name="has" defaultValue={filters.has} onChange={handleHasChange}>
                                                <ToggleButton variant="filter" value={0}>All</ToggleButton>
                                                <ToggleButton variant="filter" value={1}>
                                                    <span className="border px-1">Card</span>
                                                </ToggleButton>
                                                <ToggleButton variant="filter" value={2}>
                                                    <span className="border px-1">Course</span>
                                                </ToggleButton>
                                                <ToggleButton variant="filter" value={3}>
                                                    <span className="border px-1">Course + card</span>
                                                </ToggleButton>
                                            </ToggleButtonGroup>
                                        </ButtonToolbar>
                                        {/*  */}
                                        <ButtonToolbar className="bg-white d-inline-block rounded py-2 text-dark ml-3">
                                            <ToggleButtonGroup type="radio" name="status" defaultValue={filters.status} onChange={handleStatusChange}>
                                                <ToggleButton variant="filter" value={0}>All</ToggleButton>
                                                <ToggleButton variant="filter" value={1} className="font-weight-bold">
                                                    PAID
                                                </ToggleButton>
                                                <ToggleButton variant="filter" value={2} className="font-weight-bold">
                                                    PENDING
                                                </ToggleButton>
                                                <ToggleButton variant="filter" value={3} className="font-weight-bold">
                                                    COMPLETE
                                                </ToggleButton>
                                            </ToggleButtonGroup>
                                        </ButtonToolbar>
                                        {/*  */}
                                        <ButtonToolbar className="bg-white d-inline-block rounded py-2 text-dark ml-3">
                                            <ToggleButtonGroup type="radio" name="notes" defaultValue={filters.notes} onChange={handleNotesChange}>
                                                <ToggleButton variant="filter" value={0}>All</ToggleButton>
                                                <ToggleButton variant="filter" value={1}>
                                                    <span className="border px-2">N</span>
                                                </ToggleButton>
                                                <ToggleButton variant="filter" value={2}>
                                                    <span className="border px-2"><del>N</del></span>
                                                </ToggleButton>
                                            </ToggleButtonGroup>
                                        </ButtonToolbar>
                                    </div>
                                </Accordion.Collapse>
                            </Col>
                        </Row>
                    </Container>
                </Accordion>
            </Row>
            <Row>
                <MainSection>
                    <Container>
                        <Row>
                            <Col sm={12}>
                            {filteredList.length ? 
                                <DataSort 
                                    key={filters}
                                    data={filteredList}
                                    defaultSortBy={defaultSortBy}
                                    searchInKeys={searchInKeys}
                                    defaultDirection={'desc'}
                                    paginate
                                    itemsPerPage={itemsPerPage}
                                    render={({
                                        data,
                                        setSortBy,
                                        sortBy,
                                        direction,
                                        activePage,
                                        toggleDirection,
                                        goToPage,
                                        nextPage,
                                        prevPage,
                                        pages
                                    }) => (
                                        <Container>
                                            <style type="text/css">{`
                                                .filtered-item {
                                                    color:#777;
                                                    font-size:14px
                                                }
                                                .tag {
                                                    padding: 1px 3px;
                                                    border:1px solid #0098E8;
                                                    color:#0098E8;
                                                    font-size:12px
                                                }
                                                .has-wrapper span + span {
                                                    margin-left:5px
                                                }
                                                .tag.yellow {
                                                    border-color:#BFB10C;
                                                    color:#BFB10C;
                                                }
                                                .time-field, .status-field {
                                                    font-size:12px;
                                                    color:#999
                                                }
                                                .created-field {
                                                    color:#5688A0;
                                                    font-weight:bold;
                                                }
                                                .status-field {
                                                    font-weight:bold;
                                                    color:#999;
                                                    text-transform:uppercase
                                                }
                                                .status-field::after {
                                                    content: "Undefined"
                                                }
                                                .status-paid::after {
                                                    content: "Paid"
                                                }
                                                .status-complete::after {
                                                    content: "Complete";
                                                    color:#07CC00
                                                }
                                                .status-pending::after {
                                                    content: "Pending";
                                                    color:#FF0000
                                                }
                                            `}</style>
                                            {data.map(({ id, created, type, first, last, test, centre, date, time, has, status, note }) => (
                                                <Row key={id} className="bg-white py-3 mb-1 filtered-item">
                                                    <Col sm={1} className="created-field">
                                                        {created}
                                                    </Col>
                                                    <Col sm={2}>
                                                        {type === 1 ? 
                                                            <Globe size={15}/>: 
                                                            <PhoneInTalk size={15} />
                                                        } 
                                                        <span className="ml-2">{first} {last}</span>
                                                    </Col>
                                                    <Col sm={2}>
                                                        {test}
                                                    </Col>
                                                    <Col sm={2}>
                                                        {centre}
                                                    </Col>
                                                    <Col sm={3} className="d-flex align-items-center">
                                                        {date} 
                                                        <b className="mx-2 time-field">
                                                            {["AM", "PM"][time - 1]}
                                                        </b>
                                                        <div className="has-wrapper">
                                                            <span className="tag">Test</span>
                                                            {(has === 1 || has === 3) && <span className="yellow tag">Card</span>}
                                                            {(has === 2 || has === 3) && <span className="yellow tag">Course</span>}
                                                        </div>
                                                    </Col>
                                                    <Col sm={2} className="d-flex align-items-center">
                                                        <span className={`status-field status-${["paid", "pending", "complete"][status - 1]}`} />
                                                        {!!note.length && <span className="tag ml-auto">N</span>}
                                                    </Col>
                                                </Row>
                                            ))}
                                        </Container>
                                    )}
                                />:
                                <p>No result</p>
                            }
                            </Col>
                        </Row>
                    </Container>
                </MainSection>
            </Row>
        </Container>
    )
}

const connectedApp = connect(state => ({
    filters: filtersSelector(state),
    filteredList: filteredListSelector(state)
}), {
    filterBy
})(App)


export default connectedApp