import {createSelector} from 'reselect'
import * as JsSearch from 'js-search'

export const filtersSelector = state => state.filters

export const dataSelector = state => state.data

export const filteredListSelector = createSelector(
    filtersSelector,
    dataSelector,
    (filters, data) => {
        const {search, notes, ...otherFilters} = filters

        const list = data.filter(item => {
            for(let key in otherFilters){
                if(filters[key] === 0) continue
                if(item[key] !== filters[key]) return false
            }

            return notes === 1 ? item.note.length > 0: 
            notes === 2 ? item.note.length < 1: 
            true
        })

        const jssearch = new JsSearch.Search('id')

        jssearch.addIndex('first')
        jssearch.addIndex('last')
        jssearch.addIndex('test')
        jssearch.addIndex('centre')
        jssearch.addDocuments(list)

        return search !== '' ? jssearch.search(search): list
    }
)